import axios from 'axios'

// ajax

export function getClassifyName() {
  console.log ( 'getClassifyName ' )
  return axios.get('/api/classify')  // promise
}
export function getHome() {
  console.log ( 'getHome ' )
  return axios.get('/api/home')  // promise
}



